.nav_link:before {
  content: " ";
  width: 100%;
  height: 2px;
  transform-origin: 0;
  background-color: #ff001b;
  transition: transform .35s;
  position: absolute;
  bottom: -5px;
  left: 0;
  transform: scaleX(0);
}

.nav_link:hover:before {
  width: 100%;
  transform: scaleX(1);
}

.nav_link:not(:last-child) {
  margin-right: 40px;
}

.active-header:after {
  content: "";
  width: 100%;
  height: 2px;
  background-color: #ff001b;
  border-radius: 2px;
  position: absolute;
  bottom: -5px;
  left: 0;
}

